/*  Article diaries Thumbnail
*
*  Version:
*    1.0.0 - 2021/12/23
*/

.o-article-diaries-thumbnail {

  &__article {
    display: block;
    text-align: center;
  }

  &__images-container {
    position: relative;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    &:hover {

      .o-article-diaries-thumbnail__play-button {
        transform: translate(-50%, -50%) scale(1);
      }
    }
  }
  
  &__play-button {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%) scale(.9);
    @include t-width-and-height(73);
    display: block;
    margin: 0 auto;
    transition: transform $s-anim--time-short $s-anim--ease-in-out;

    .icon--play {
      &-inner {
        fill: $s-color--background-tertiary;
      }
    }
  }

  &__article-image-container {
    padding-bottom: 100%;
    grid-row: span 1;
    grid-column: span 1;
  }

  &__article-gif-container {
    padding-bottom: 0;
    cursor: pointer;
    visibility: hidden;
    opacity: 0;
    transition: opacity 1s;
    grid-row: span 1;
    grid-column: span 1;
  }

  &__article-title {
    text-align: center;
    margin-top: 1.5rem;
  }

  &__article-btn {
    margin: 1.125rem auto 0;
    display: block;
    text-align: center;
    width: max-content;
  }

  @include t-media-query($s-large-up) {
    &__article-title {
      margin-top: 1.1875rem;
    }
  }
}