@import '../base.scss';

@import '../tools/objects/article/article-diaries-thumbnail';
@import '../tools/objects/article/article-stories-thumbnail';
@import '../tools/objects/article/social-video-thumbnail';

.c-articles-slider {

  &__inner {
    padding-top: 2.5625rem;
    padding-bottom: 3.4375rem;
  }

  &__pretitle {
    text-align: center;
    text-transform: capitalize;
    width: 80%;
    margin: 0 auto;
  }

  &__title {
    text-align: center;
    text-transform: capitalize;
    width: 80%;
    margin: 1.875rem auto 2.75rem;
  }

  &__articles {
    text-align: center;
  }

  &__btn-container {
    text-align: center;
  }

  @include t-media-query($s-large-up) {
    &__inner {
      padding-top: 4rem;
      @include t-gutters;
    }

    &__diaries-wrapper,
    &__stories-wrapper,
    &__social-video-wrapper {
      &:not(:last-child) {
        margin-right: 3.125rem;
      }
    }
  }

  @include t-media-query($s-xxlarge-up) {
    &__inner {
      padding-top: 9.375rem;
    }

    &__title {
      margin: 1.125rem auto 5.125rem;
    }

    &__diaries-wrapper,
    &__stories-wrapper,
    &__social-video-wrapper {
      &:not(:last-child) {
        margin-right: 6.125rem;
      }
    }
  }
}