.o-social-video-thumbnail {
  position: relative;

  &__image-wrapper {

    position: relative;

    &:hover {
      .o-social-video-thumbnail__play-button {
        transform: translate(-50%, -50%) scale(1);
      }
    }

    &::after {
      position: absolute;
      content: '';
      inset: 0 0 0 0;
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1));
    }
  }

  &__image-container {
    padding-bottom: 162.5%;
  }

  &__play-button {
    cursor: pointer;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%) scale(.9);
    width: 4.5625rem;
    height: 4.5625rem;
    display: block;
    transition: transform $s-anim--time-short $s-anim--ease-in-out;

    .icon--play {
      &-inner {
        fill: $s-color--background-tertiary;
      }
    }
  }

  &__content {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    color: $s-color--font-6;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0 1.5rem 3.125rem;
  }

  &__pretitle-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 1.25rem;
  }

  &__pretitle {
    color: $s-color--font-6;
    margin-left: 0.5625rem;
    margin-top: 0.3125rem;
  }

  &__title {
    margin-bottom: 1.125rem;
  }

  &__author {
    color: $s-color--font-6;
  }

  @include t-media-query($s-small-up) {
    &__content {
      padding: 5% 1.5rem 8%;
    }

    &__pretitle {
      font-size: 1vw;

      &-wrapper {
        margin-bottom: 3%;
      }
    }

    &__title {
      margin-bottom: 5%;
      font-size: 2vw;
    }

    &__author {
      font-size: 1vw;
    }
  }

  @include t-media-query($s-large-up) {

    &__image-container {
      padding-bottom: 153%;
    }

    &__play-button {
      width: 6.25rem;
      height: 6.25rem;
    }

    &__title {
      margin-bottom: 1.375rem;
    }
  }

  @include t-media-query($s-xlarge-up) {

    &__author,
    &__pretitle {
      @include t-font--heading-7;
    }

    &__title {
      @include t-font--heading-6;
      margin-bottom: 1.625rem;
    }

    &__content {
      padding: 5% 2.1875rem 9%;
    }
  }
}